<template lang="pug">
div
	.factorian-content-block.factorian-breadcroumb.section-gray
		.container(v-if="Global.noticias.get")
				.row
					.col-md-6
						h2 {{ Global.noticias.get.titulo }}

				.row
					.col-md-8
						article.post.wow.fadeInUp(style="visibility: visible; animation-name: fadeInUp;" )
							.featured-content
									img(:src="Global.url +'uploads/post/' + Global.noticias.get.imagen")

							.post-title-wrap
									p {{ Global.noticias.get.bajada }}
									p
										small {{ Global.noticias.get.creado.date | moment("dddd, DD [de] MMMM [de] YYYY") }}
							
							.entry-content(v-html="Global.noticias.get.texto")
						
						
					.col-md-4
						.widget.recent_posts.wow.fadeInUp(style="visibility: visible; animation-name: fadeInUp;")
							h3.widget-title Noticias Recientes
							ul
								li(v-if="Global.noticias.relacionadas.length > 0" v-for="noticia in Global.noticias.relacionadas")
									a(@click="verNoticia(noticia)")  
										span {{ noticia.titulo}} 
										span {{ noticia.creado.date | moment("dddd, DD [de] MMMM [de] YYYY") }}
								

				
</template>

<script>
import { mapState } from 'vuex'
export default {
   
   computed: {
      ... mapState(['Global']) 
   },
   created () {
		
			this.$store.dispatch('Global/VER_NOTICIA', this.$route.params.id )
			this.$store.dispatch('Global/VER_RELACIONADAS', this.$route.params.id )
		
      
   },
	/* watch: {
		// whenever question changes, this function will run
		Global.noticias.get.id: function (id) {
			this.answer = 'Waiting for you to stop typing...'
			this.getAnswer()
		}
	}, */
	methods: {
		verNoticia(noticia){
			this.$store.dispatch('Global/VER_NOTICIA', noticia.id )
			this.$store.dispatch('Global/VER_RELACIONADAS', noticia.id )
			this.$router.push({ name: 'verNoticia', params: { id: noticia.id }})
		}
	} 
}
</script>

<style>

.foto {
    height: 150px;
    background-position: center;
    background-size: cover !important;
}
</style>


